import React, { Component } from 'react';
import Poll from 'react-polls';

// Declaring poll question and answers
const pollQuestion = 'کیفیت وبسایت چطوره ؟'
const pollAnswers = [
  { option: 'عالی', votes: 26},
  { option: 'خوب', votes: 14},
  { option: 'بد', votes: 5},
  { option: 'افتضاح', votes: 6 }
]

class PollOnline extends Component {
  // Setting answers to state to reload the component with each vote
  state = {
    pollAnswers: [...pollAnswers]
  }

  // Handling user vote
  // Increments the votes count of answer when the user votes
  handleVote = voteAnswer => {
    const { pollAnswers } = this.state
    const newPollAnswers = pollAnswers.map(answer => {
      if (answer.option === voteAnswer) answer.votes++
      return answer
    })
    
    this.setState({
      pollAnswers: newPollAnswers
    })
  }

  render () {
    const { pollAnswers } = this.state
    return (
      <div>
        <Poll question={pollQuestion} customStyles={{theme: 'blue',align: 'right',questionColor: '#FFF'}} answers={pollAnswers} onVote={this.handleVote} />
      </div>
    );
  }
};


export default PollOnline