import "./contact.css";
import Phone from "./Img/call.svg";
import Email from "./Img/email.svg";
import Address from "./Img/location.svg";
import { useRef, useState } from "react";
import emailjs from "emailjs-com";

const Contact = () => {
  const formRef = useRef();
  const [done, setDone] = useState(false)
  
  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_0r73w3c",
        "template_j7kn0zs",
        formRef.current,
        "user_azlrlpzzE1XJR4Uq2bADX"
      )
      .then(
        (result) => {
          console.log(result.text);
          setDone(true)
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div className="c" style={{marginTop: '6%'}}>
      <div className="c-bg"></div>
      <div className="c-wrapper">
        <div className="c-left">
          <h1 className="c-title">همیشه در کنار شما</h1>
          <div className="c-info">
            <div className="c-info-item" style={{color: 'White'}}>
              <img src={Phone} alt="" className="c-icon" />
              +98 921 204 1091
            </div>
            <div className="c-info-item" style={{color: 'White'}}>
              <img className="c-icon" src={Email} alt="" />
              amirshooter1453@gmail.com
            </div>
            <div className="c-info-item" style={{color: 'White'}}>
              <img className="c-icon" src={Address} alt=""/>
            ایران/اراک , یه جایی
            </div>
          </div>
        </div>
        <div className="c-right">
          <p className="c-desc" >
            <b>سوال یا دارید ؟</b> در تماس باشید. همیشه در دسترس و آماده پاسخگویی هستیم
          
          </p>
          <form className='c-form' ref={formRef} onSubmit={handleSubmit}>
            <input className='c-userForm' type="text" placeholder="نام کامل شما" name="user_name"  />
            <input className='c-userForm' type="text" placeholder="موضوع" name="user_subject" />
            <input className='c-userForm' type="text" placeholder="ایمیل" name="user_email" />
            <textarea className='c-userMessage' rows="5" placeholder="پیام شما ..." name="message" />
            <button className='c-submitBtn'>ارسال پیام</button>
            {done && <div style={{color: 'red',fontSize: '2rem'}}>Thank you...</div>}
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
