import React from 'react'
import About from './assets/img/about2.jpg'
import Perfil from './assets/img/perfil22.png'
import './styles.css'
import { BiSend } from 'react-icons/bi'
import { BsYoutube, BsDribbble, BsGithub } from 'react-icons/bs'
import { FiMusic } from 'react-icons/fi'

function Alexa() {
  return (
    <>
    <main className="main">
   
    <section className="section">
        <div className="home__container container-aut grid-aut">
            <div className="home__content grid-aut">
                <div className="home__social">
                    <a className="home__social-icon">
                        <BsYoutube size='1em'/>
                    </a>
                    <a className="home__social-icon">
                        <BsDribbble />
                    </a>
                    <a className="home__social-icon">
                        <BsGithub />
                    </a>
                </div>

                <div className="home__img">
                    <svg className="home__blob" viewBox="0 0 200 187" xmlns="http://www.w3.org/2000/svg" >
                    <mask id="mask0" mask-type="alpha">
                            <path d="M190.312 36.4879C206.582 62.1187 201.309 102.826 182.328 134.186C163.346 165.547 
                            130.807 187.559 100.226 186.353C69.6454 185.297 41.0228 161.023 21.7403 129.362C2.45775 
                            97.8511 -7.48481 59.1033 6.67581 34.5279C20.9871 10.1032 59.7028 -0.149132 97.9666 
                            0.00163737C136.23 0.303176 174.193 10.857 190.312 36.4879Z"/>
                        </mask>
                        <g mask="url(#mask0)">
                            <path d="M190.312 36.4879C206.582 62.1187 201.309 102.826 182.328 134.186C163.346 
                            165.547 130.807 187.559 100.226 186.353C69.6454 185.297 41.0228 161.023 21.7403 
                            129.362C2.45775 97.8511 -7.48481 59.1033 6.67581 34.5279C20.9871 10.1032 59.7028 
                            -0.149132 97.9666 0.00163737C136.23 0.303176 174.193 10.857 190.312 36.4879Z"/>
                        <image className="home__blob-img" x="12" y="18" xlinkHref={Perfil}/>
                        </g>
                    </svg>
                </div>
                
                <div className="home__data">
                    <h1 className="home__title">سلام, امیرمهدی هستم</h1>
                    <h3 className="home__subtitle">برنامه نویس حرفه ای</h3>
                    <p className="home__description" style={{color: '#6d6a7c'}}>
              توسعه دهنده این وبسایت : امیرمهدی ابراهیمی پایه دهم رشته ریاضی ناحیه یک اراک, کلاس 101, مدرسه شکرایی
                    </p>
                    <a className="button button--flex" href='#damnbro' style={{textDecoration: 'none'}} >
                    <FiMusic size='1.5em' className='icons-music-fi'/>  درمورد وبسایت  <FiMusic size='1.5em' className='icons-music-fi2'/>
                    </a>
                </div>
            </div>
        </div>
    </section>

    
    <section className="about section" id='damnbro'>
        <h2 className="section__title" style={{color: 'black'}}>در مورد وبسایت</h2>
        <span className="section__subtitle" style={{color: '#625563'}}>معرفی نامه</span>

        <div className="about__container container-aut grid-aut">
            <img src={About} alt="" className="about__img" />

            <div className="about__data">
                <p className="about__description" style={{color: '#6d6a7c'}}>
             وبسایت یوکا برای موسیقی و زندگی , شما میتوانید به صورت نامحدود موسیقی گوش دهید , پلی لیست ذخیره کنید, بلاگ و اخبار مربوطه را به صورت زنده ببینید و اوقات فراغت خود را بگذرانید و این وبسایت برای جشنواره فرهنگی و هنری فردا (1401) ساخته شده 
                </p>
                <div className="about__info">
                    <div>
                        <span className="about__info-title" style={{color: 'black'}}>02+</span>
                        <span className="about__info-name" style={{color: '#6d6a7c'}}>سال <br /> تجربه</span>
                    </div>
                    <div>
                        <span className="about__info-title" style={{color: 'black'}}>20+</span>
                        <span className="about__info-name" style={{color: '#6d6a7c'}}>کامل شده <br /> پروژه</span>
                    </div>
                    <div>
                        <span className="about__info-title" style={{color: 'black'}}>20</span>
                        <span className="about__info-name" style={{color: '#6d6a7c'}}>معدل <br /></span>
                    </div>
                </div>

                
            </div>
        </div>
    </section>
</main>
   
</>
    )
}

export default Alexa
