import React from 'react'
import { BsFillPlayCircleFill } from 'react-icons/bs'
import './style.css'
import Cards from './Cards'

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Avatar from './Images/1.jpg'
import Avatar2 from './Images/show52.jpg'
import Avatar3 from './Images/show42.jpg'
import Avatar4 from './Images/62.jpg'
import Avatar5 from './Images/42.jpg'

import bg from './assets/banner.svg'
import bg2 from './assets/banner2.svg'
import bg3 from './assets/banner3.svg'

import cute1 from './Images/12.png'
import cute2 from './Images/22.png'
import cute3 from './Images/3-(2)2.png'
import Image from './Images/image12.jpg';
import Banner from './Images/banner22.jpg'
import Banner2 from './Images/banner23.jpg'

function Musics() {
  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: false
      },
      }
    ]
  };

    const openInNewTab = (url) => {
        const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
        if (newWindow) newWindow.opener = null
    }
    return (
  <div className='mainPage-Msc' style={{padding: '2rem'}}>
      <h3 className='music-card-title' style={{color: '#FFFF', textAlign: 'center'}}>بازدیدهای محبوب</h3>
 <div className="mainDivMiscs">
   <div className="MainCardandBibilak">
   <div className='banner_cards_grid'>
   <div className='discover_cards'>
     <img className='img-banner-dsv' src={bg} alt='adash'/>
   <div className='discover-details'>
     <img className='discover-cutie' src={cute1} alt='damn'/>
        <div className='discover-card-info'>
          <BsFillPlayCircleFill onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/tophits')} color='#FFF' size='2.2em' style={{marginBottom: '5%'}}/>
          <h3 className='discover-info-title'>Fox The Headphone</h3>
        </div>
   </div>
    </div>
    <div className='discover_cards'>
     <img className='img-banner-dsv' src={bg2} alt='adash'/>
   <div className='discover-details'>
   <img className='discover-cutie' src={cute2} alt='damn'/>
        <div className='discover-card-info'>
          <BsFillPlayCircleFill onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/farcry5')} color='#FFF' size='2.2em' style={{marginBottom: '5%'}}/>
            <h3 className='discover-info-title'>Cat The hedphone</h3>
        </div>
   </div>
    </div>
    <div className='discover_cards'>
     <img className='img-banner-dsv' src={bg3} alt='adash'/>
   <div className='discover-details'>
   <img className='discover-cutie' src={cute3} alt='damn'/>
        <div className='discover-card-info'>
          <BsFillPlayCircleFill onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/devillady')} color='#FFF' size='2.2em' style={{marginBottom: '5%'}}/>
            <h3 className='discover-info-title'>Turtle The Hedgehog</h3>
        </div>
   </div>
    </div>
    <div className='discover_cards'>
     <img className='img-banner-dsv' src={bg} alt='adash'/>
   <div className='discover-details'>
   <img className='discover-cutie' src={cute1} alt='damn'/>
        <div className='discover-card-info'>
          <BsFillPlayCircleFill onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/donya')} color='#FFF' size='2.2em' style={{marginBottom: '5%'}}/>
            <h3 className='discover-info-title'>Fox The Hedgehog</h3>
        </div>
   </div>
    </div>
   </div>

<div className="ahayMain">
  <h3 className='ahay-title'>Popular Available Now</h3>
  <div className="ahay">
            <div onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/devillady')} className='music-card-main'>
                <img src={Avatar} className='music-card-img'/>
            </div>
            <div onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/dailymix3')} className='music-card-main'>
                <img src={Avatar2} className='music-card-img'/>
            </div>
            <div onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/donya')} className='music-card-main'>
                <img src={Avatar3} className='music-card-img'/>
            </div>
            <div onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/haftalikkesif')} className='music-card-main'>
                <img src={Avatar4} className='music-card-img'/>
            </div>
            <div onClick={() => openInNewTab('https://open-uka.vercel.app/playlist/dailymix2')} className='music-card-main'>
                <img src={Avatar5} className='music-card-img'/>
            </div>
  </div>
</div>


  </div>
  
    <div class="card rgb" >
      <div class="card-image">
     <img className='card-img-my' src={Image}/>
      </div>
      <div class="card-text">
        <span class="date">3 روز پیش</span>
        <h2>آپدیت پخش هنرمندان</h2>
        <p>پخش هنرمندان روی وبسایت بارگزاری شد و اصلاح شد در روز های اولیه شاید به مشکل بربخورید ولی سعی میکنم که همه آنها را شناسایی به موقع بر طرف کنم با تشکر از کسانی که این مشکل را به ما اطلاع دهید. ممنون </p>
        <p>💖💖</p>
        <div className='music-cards-fist'>
        </div>
      </div>
    </div>
    </div>

  <div>
    <div className='iWannaDie'>
     <Slider {...settings}>  
          <div className='music-hits'>
                  <img className='music-hits-img' src={Banner} alt=''/>
          </div>
           <div className='music-hits'>
                <img className='music-hits-imgTwo' src={Banner2} alt=''/>  
          </div>
      </Slider>
    </div>

  </div>

  <div className="div-card" style={{marginTop: '10vh'}}>
    <h3 className="ahay-title">Popular Tracks</h3>
  <Cards />
  </div>

    
    </div>
    )
}

export default Musics
