import React from "react";
import Slider from "react-slick";
import { sliderImages, imageSliderConfig } from "./utils";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./style.css";

const imageSlider = () => {
  return (
    <article className='banner-imgs-article' >
      <Slider className="imageSlider" {...imageSliderConfig}>
        {sliderImages.map((image) => (
          <div className="slider__container" key={image.id}>
            <img className="slider__textImage" src={image.textImage} alt='' />
            <div class="slider__imgContainer">
              <img className="slider__img" src={image.url} alt=''  />
            </div>
          </div>
        ))}
      </Slider>
    <div className='responsive-article'>
      <img className='responsive-banner' src="https://cdn.discordapp.com/attachments/853555124621934602/938076656004890660/W2.png"/>
      <img className='responsive-text' src="https://cdn.discordapp.com/attachments/853555124621934602/938070998547304508/2scale.png"/>
    </div>
    </article>
  );
};

export default imageSlider;
