import React, { useState, useEffect } from "react";
import Stars from '../images/stars.png'
import Moon from '../images/moon.png'
import Mountains_behind from '../images/mountains_behind.png'
import Mountains_front from '../images/mountains_front2.png'
import './styles.css'

function Hero() {

  const [offsetY, setOffsetY] = useState(0);
  const handleScroll = () => setOffsetY(window.pageYOffset);
     useEffect(() => {
     window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const [offsetX, setOffsetX] = useState(0);
  const handleScroll2 = () => setOffsetX(window.pageYOffset);
    useEffect(() => {
    window.addEventListener("scroll", handleScroll2);
   return () => window.removeEventListener("scroll", handleScroll2);
 }, []);
 

    return (
    <section className="hero-section">
      <img className='Stars' id="stars" src={Stars} alt="stars" style={{ transform: `translateX(${offsetX * 0.25}px)` }}/>
      <img className='Moon' id="moon" src={Moon} alt="moon" style={{ top: `${offsetY * 1.05}px`}}/>
      <img className='Mountains_behind' style={{ transform: `translateY(${offsetY * 0.5}px)`}} id="mountains_behind" src={Mountains_behind} alt='mountains_behind' />
      <h2 className='Get-Started' style={{ marginRight: `${offsetY * 4}px`,marginTop: `${offsetY * 1.5}px`}} >موسیقی برای همه</h2>
      <a className='btn-explore' style={{ marginTop: `${offsetY * 1.5}px`}} href="#sec" id="btn">ماجراجویی</a>
      <img src={Mountains_front} style={{ transform: `translateY(-${offsetY * 0}px)`}} id="mountains_front" alt="mountains_front" />
    </section>
    )
}

export default Hero
