import React from 'react'
import Section1 from './Images/22.png'
import Section2 from './Images/11.png'
import Section3 from './Images/33.png'
// import Section4 from './Images/section-4.svg'
// import Sparkles from './Images/sparkles.svg'
import './styles.css'


function Content() {
  const FontColors = '#FFF'
  return (
    <>
    <section className="collaboration">
      <div className="row-cont container-cont ">
        <img
          src={Section1}
          alt="collaboration"
          className="grid-image"
        />
        <div className="text-group">
          <h2 className="title secondary-title" style={{ color : FontColors }}>
          گوش دادن بدون محدودیت
          </h2>
          <p style={{ color : FontColors }}>
          هدفون خود را روی گوش خود قرار دهید و هر آهنگی (حتی با موبایل) را پخش کنید و شروع به لذت بردن از آن کنید :)
          </p>
        </div>
      </div>
    </section>

    <section className="voice-channel">
      <div className="row-cont container-cont ">
        <img
          src={Section2}
          alt="collaboration"
          className="grid-image order-2"
          style={{width: '85%'}}
        />
        <div className="text-group order-1">
          <h2 className="title secondary-title" style={{ color : FontColors }}>شنوندگان شما</h2>
          <p style={{ color : FontColors }}>
          این وب سایت جایی است که کشف موسیقی برای بیش از 380 میلیون شنونده اتفاق می افتد. خواه هنرمند معتبری باشید، این تنها راه برای ارائه آهنگ‌های جدید به سردبیران برخی از فهرست‌های پخش جهان است که بیشترین دنبال‌کردن را دارند.
          </p>
        </div>
      </div>
    </section>

    <section className="moderation-tools">
      <div className="row-cont container-cont ">
        <img
          src={Section3}
          alt="moderation tools"
          className="grid-image order-2"
        />
        <div className="text-group order-1">
          <h2 className="title secondary-title" style={{ color : FontColors }}>هنرمندان</h2>
          <p style={{ color : FontColors }}>
          آهنگ تو. طرفداران شما تیم شما. الان همه با هم همه ابزارهایی که برای ایجاد فالوور و حرفه خود در Uka نیاز دارید، همه در یک مکان.
          </p>
        </div>
      </div>
    </section>
        </>
    )
}

export default Content
