import React from 'react';
import About from './Images/LogoTEXT.png';
import AboutBox from './Images/discord_nitro.png';
import './styles.css';

function content() {
    return (
        <>
        <section className='section-nitro' >
            <div className="content-nitro">
                <div className="textBox-nitro">
                    <img  src={About} alt=''/>
                    <p>An advanced experience of the Uka app. Listen to music without restrictions, create playlists, personalize and always have fun</p>
                </div>
                <div className="imgBox-nitro">
                    <img className='textBOX-NITRO-img' src={AboutBox} alt=''/>
                </div>
            </div>
        </section>
        </>
    )
}

export default content;
