import './App.css';
import Navbar from './components/navbar2/Navbar';
import Hero from './components/hero/hero'
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Blog from './components/blog/pages/Blog';
import BlogWeb from './components/blog/pages/BlogWeb'
import Artists from './components/pages/artists/artists';
import Contents from './components/pages/info/Content/Content'
import Contact from './components/pages/contact/Components/contact/Contact';
import AboutUs from './components/pages/aboutUs/AboutUs'
import Footer from './components/footer/Footer'
import ReactWeather, { useOpenWeather } from 'react-open-weather';
import Poll from './components/poll/pollOnline'


function App() {
  const time = new Date().toLocaleTimeString();
  const { data, isLoading, errorMessage } = useOpenWeather({
    key: '376eaea4930959eb0d4b91257cba2ae5',
    lat: '48.137154',
    lon: '11.576124',
    lang: 'fa',
    unit: 'metric', // values are (metric, standard, imperial)
  });

  return (
    <div className="App">
<BrowserRouter>
  <Switch>
    <Route exact path="/">
     <Navbar />
     <Hero />
    <h3 className='timeDate'>{time}</h3>
      {/* <BlogWeb /> */}
    <div className="sec" id="sec"> 
    <Contents />
    </div>
  <div className="PollAndWeather" id="sec2">

    <ReactWeather
      isLoading={isLoading}
      errorMessage={errorMessage}
      data={data}
      lang="fa"
      locationLabel="Arak"
      unitsLabels={{ temperature: 'C', windSpeed: 'Km/h' }}
      showForecast={false}
    />
    <Poll/>
  </div>
    </Route>
    <Route exact path='/blog/:id'>
     <Navbar />
     <Blog/>
    </Route>
    <Route exact path='/artists'>
     <Navbar />
     <Artists/>
    </Route>
    <Route exact path='/contact-us'>
     <Navbar />
     <Contact/>
    </Route>
    <Route exact path='/blog'>
     <Navbar />
     <BlogWeb/>
    </Route>
    <Route exact path='/about-us'>
     <Navbar />
     <AboutUs/>
    </Route>
  </Switch>
  <Footer />
</BrowserRouter>
    </div>
  );
}

export default App;
