import React from 'react';
import './cards.css';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import BackdropFilter from "react-backdrop-filter";
import Avatar1 from './Images/1.jpg';
import Avatar2 from './Images/2.jpg';
import Avatar3 from './Images/3.png';
import Avatar4 from './Images/4.png';
import Avatar5 from './Images/5.png';
import Avatar6 from './Images/6.png';

function Cards() {
  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 5,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 1,
          infinite: true,
          dots: false
        }
      },
      {
        breakpoint: 730,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          initialSlide: 2
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          initialSlide: 2
        }
      },
      {
        breakpoint: 520,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1
        }
      },
      {
        breakpoint: 420,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
  const openInNewTab = (url) => {
     const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
     if (newWindow) newWindow.opener = null
    }
  return (
      <div className='fully-top-music-div'>
      <Slider {...settings}>  
      <div className='music-wrapper'>
          <div className='music-card'>
              <div className='music-overflow' onClick={() => openInNewTab('https://google.com')}>
                  <img src={Avatar1} alt='music banner' className='music-img-top' width='180px'/>
              </div>
              <div className='music-body'>
              <BackdropFilter
              className="bluredDetails"
              filter={"blur(40px)"}
              >
                  <h4 className='song-title'>Devil Lady<br></br><span className='song-artist'>LOAT!</span></h4>
                  {/* <botton onClick={() => openInNewTab('https://google.com')} className='play-btn'><PlayCircleFilledIcon className='play-music-btn' fontSize="large"/></botton> */}
              </BackdropFilter>
              </div>
          </div>
      </div>
      <div className='music-wrapper'>
          <div className='music-card'>
              <div className='music-overflow' onClick={() => openInNewTab('https://google.com')}>
                  <img src={Avatar2} alt='music banner' className='music-img-top' width='180px'/>
              </div>
              <div className='music-body'>
              <BackdropFilter
              className="bluredDetails"
              filter={"blur(40px)"}
              >
                  <h4 className='song-title'>Industry Baby<br></br><span className='song-artist'>Lil Nas X</span></h4>
                  {/* <botton onClick={() => openInNewTab('https://google.com')} className='play-btn'><PlayCircleFilledIcon className='play-music-btn' fontSize="large"/></botton> */}
              </BackdropFilter>
              </div>
          </div>
      </div>
      <div className='music-wrapper'>
          <div className='music-card'>
              <div className='music-overflow' onClick={() => openInNewTab('https://google.com')}>
                  <img src={Avatar3} alt='music banner' className='music-img-top' width='180px'/>
              </div>
              <div className='music-body'>
              <BackdropFilter
              className="bluredDetails"
              filter={"blur(40px)"}
              >
                  <h4 className='song-title'>Lavandia<br></br><span className='song-artist'>Arash, Marshmello</span></h4>
                  {/* <botton onClick={() => openInNewTab('https://google.com')} className='play-btn'><PlayCircleFilledIcon className='play-music-btn' fontSize="large"/></botton> */}
              </BackdropFilter>
              </div>
          </div>
      </div>
      <div className='music-wrapper'>
          <div className='music-card'>
              <div className='music-overflow' onClick={() => openInNewTab('https://google.com')}>
                  <img src={Avatar4} alt='music banner' className='music-img-top' width='180px'/>
              </div>
              <div className='music-body'>
              <BackdropFilter
              className="bluredDetails"
              filter={"blur(40px)"}
              >
                  <h4 className='song-title'>Still Alive<br></br><span className='song-artist'>Portal</span></h4>
                  {/* <botton onClick={() => openInNewTab('https://google.com')} className='play-btn'><PlayCircleFilledIcon className='play-music-btn' fontSize="large"/></botton> */}
              </BackdropFilter>
              </div>
          </div>
      </div>
      <div className='music-wrapper'>
          <div className='music-card'>
              <div className='music-overflow' onClick={() => openInNewTab('https://google.com')}>
                  <img src={Avatar5} alt='music banner' className='music-img-top' width='180px'/>
              </div>
              <div className='music-body'>
              <BackdropFilter
              className="bluredDetails"
              filter={"blur(40px)"}
              >
                  <h4 className='song-title'>good 4 u<br></br><span className='song-artist'>Olivia Rodrigo</span></h4>
                  {/* <botton onClick={() => openInNewTab('https://google.com')} className='play-btn'><PlayCircleFilledIcon className='play-music-btn' fontSize="large"/></botton> */}
              </BackdropFilter>
              </div>
          </div>
      </div>
      <div className='music-wrapper'>
          <div className='music-card'>
              <div className='music-overflow' onClick={() => openInNewTab('https://google.com')}>
                  <img src={Avatar6} alt='music banner' className='music-img-top' width='180px'/>
              </div>
              <div className='music-body'>
              <BackdropFilter
              className="bluredDetails"
              filter={"blur(40px)"}
              >
                  <h4 className='song-title'>Beggin'<br></br><span className='song-artist'>Måneskin</span></h4>
                  {/* <botton onClick={() => openInNewTab('https://google.com')} className='play-btn'><PlayCircleFilledIcon className='play-music-btn' fontSize="large"/></botton> */}
              </BackdropFilter>
              </div>
          </div>
      </div>

      </Slider>
      </div>
  )
}

export default Cards;
